.page-navbar-bottom {
    width: 100%;
    text-align: center;
    /* position: absolute;
    bottom: 0; */
    padding: 20px;
}

.page-navbar-bottom > span {
    text-align: center;
    cursor: pointer;
    margin: 20px;
}
a {
    position: relative;
}

.page-navbar-bottom > span > a::after {
    content: '';
    width: 0;
    height: 2px;
    background: black;
    position: absolute;
    bottom: -5px;
    left: 50%;
    transform: translate(-50%);
    transition: all 0.2s;
}

.page-navbar-bottom > span:hover > a::after {
    width: 100%;
}
