* {
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

body {
    padding: 0;
    margin: 0;
    width: 100vw;
    overflow-x: hidden;
    font-family: 'Courier New', Courier, monospace;
    text-align: center;
}

#root {
    width: 100vw;
    position: relative;
}

.page-wrapper {
    position: relative;
    width: 100vw;
    margin: 0;
}

.page {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    margin: 0;
    border: 1px solid black;
}

.page-content {
    flex: 1 1 auto;
}

.page-content>h1 {
    font-family: 'Rancho', cursive;
    position: absolute;
    left: 50%;
    top: 50%;
    font-size: 6em;
    transform: translate(-50%, -50%);
}

h2 {
    margin: 0;
    padding: 20px;
}

highlight {
    color: blue;
}