.page-navbar-bottom {
    width: 100%;
    text-align: center;
    /* position: absolute;
    bottom: 0; */
    padding: 20px;
}

.page-navbar-bottom > span {
    text-align: center;
    cursor: pointer;
    margin: 20px;
}
a {
    position: relative;
}

.page-navbar-bottom > span > a::after {
    content: '';
    width: 0;
    height: 2px;
    background: black;
    position: absolute;
    bottom: -5px;
    left: 50%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    transition: all 0.2s;
}

.page-navbar-bottom > span:hover > a::after {
    width: 100%;
}

* {
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

body {
    padding: 0;
    margin: 0;
    width: 100vw;
    overflow-x: hidden;
    font-family: 'Courier New', Courier, monospace;
    text-align: center;
}

#root {
    width: 100vw;
    position: relative;
}

.page-wrapper {
    position: relative;
    width: 100vw;
    margin: 0;
}

.page {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    margin: 0;
    border: 1px solid black;
}

.page-content {
    flex: 1 1 auto;
}

.page-content>h1 {
    font-family: 'Rancho', cursive;
    position: absolute;
    left: 50%;
    top: 50%;
    font-size: 6em;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

h2 {
    margin: 0;
    padding: 20px;
}

highlight {
    color: blue;
}
